import React from 'react'
import { connect } from 'react-redux';
import { NAV_TYPE_TOP } from 'constants/ThemeConstant';
import utils from 'utils'
import PublicMenuContent from './PublicMenuContent'

export const PublicTopNav = ({topNavColor, localization = true}) => {
    const props = { topNavColor, localization }
    return (
        <div className={`top-nav ${utils.getColorContrast(topNavColor)}`} style={{backgroundColor: topNavColor}}>
            <div className="top-nav-wrapper">
                <PublicMenuContent
                    type={NAV_TYPE_TOP}
                    {...props}
                />
            </div>
        </div>
    )
}

const mapStateToProps = ({ theme }) => {
    const { topNavColor } =  theme;
    return { topNavColor }
};

export default connect(mapStateToProps)(PublicTopNav);
