import {all, takeEvery, put, fork} from 'redux-saga/effects';
import {calling} from "../default.saga";
import {PUBLIC_BOOKING_MY_LISTS, PUBLIC_BOOKING_MY_LISTS_ERROR, PUBLIC_BOOKING_MY_LISTS_SUCCESS} from "../../constants";

export function* _getBookList(){
    yield takeEvery(PUBLIC_BOOKING_MY_LISTS,function*({params}){
        try{
            let response = yield calling({url:`/api/v1/book`,config:{params}},'get')
            if(!response?.error){
                yield put({
                    type:PUBLIC_BOOKING_MY_LISTS_SUCCESS,
                    ...response
                })
            }else{
                yield put({
                    type:PUBLIC_BOOKING_MY_LISTS_ERROR,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put({
                type:PUBLIC_BOOKING_MY_LISTS_ERROR,
                message: err?.message ?? "Some Error"
            })

        }
    })
}

export default function* rootSaga() {
    yield all([
        fork(_getBookList)
    ])
}