import React, {useState, useEffect} from "react";
import {connect} from "react-redux";
import {Input, Layout} from "antd";
import {MenuFoldOutlined, MenuUnfoldOutlined, SearchOutlined} from '@ant-design/icons';
import Logo from './Logo';
import NavPanel from './NavPanel';
import NavSearch from './NavSearch';
import {toggleCollapsedNav, onMobileNavToggle} from 'redux/actions/Theme';
import {NAV_TYPE_TOP, SIDE_NAV_COLLAPSED_WIDTH, SIDE_NAV_WIDTH} from 'constants/ThemeConstant';
import utils from 'utils'
import {NavProfile} from "../NavProfile";
import PropTypes from "prop-types";
import {signOut} from "../../../redux/actions";

const {Header} = Layout;

export const PublicHeaderNav = props => {
    const {
        navCollapsed,
        mobileNav,
        navType,
        headerNavColor,
        toggleCollapsedNav,
        onMobileNavToggle,
        isMobile,
        currentTheme,
        direction
    } = props;
    const [searchActive, setSearchActive] = useState(true)

    const onSearchClose = () => {
        setSearchActive(false)
    }

    const onToggle = () => {
        if (!isMobile) {
            toggleCollapsedNav(!navCollapsed)
        } else {
            onMobileNavToggle(!mobileNav)
        }
    }

    const isNavTop = navType === NAV_TYPE_TOP
    const mode = () => {
        if (!headerNavColor) {
            return utils.getColorContrast(currentTheme === 'dark' ? '#00000' : '#ffffff')
        }
        return utils.getColorContrast(headerNavColor)
    }
    const navMode = mode()
    const getNavWidth = () => {
        if (isNavTop || isMobile) {
            return '0px'
        }
        if (navCollapsed) {
            return `${SIDE_NAV_COLLAPSED_WIDTH}px`
        } else {
            return `${SIDE_NAV_WIDTH}px`
        }
    }

    useEffect(() => {
        if (!isMobile) {
            onSearchClose()
        }
    })


    return (
        <Header className={`app-header ${navMode}`} style={{backgroundColor: headerNavColor}}>
            <div className={`app-header-wrapper ${isNavTop ? 'layout-top-nav' : ''}`}>
                <Logo logoType={navMode}/>
                <div className="nav" style={{width: `calc(100% - ${getNavWidth()})`}}>
                    <div className="nav-left">
                        <ul className="ant-menu ant-menu-root ant-menu-horizontal">
                            {
                                isNavTop && !isMobile ?
                                    null
                                    :
                                    <li className="ant-menu-item ant-menu-item-only-child" onClick={() => {
                                        onToggle()
                                    }}>
                                        {navCollapsed || isMobile ? <MenuUnfoldOutlined className="nav-icon"/> :
                                            <MenuFoldOutlined className="nav-icon"/>}
                                    </li>
                            }
                        </ul>
                    </div>
                    <div className="nav-right">
                        <NavProfile {...props} />
                        {/*<NavPanel direction={direction} />*/}
                    </div>
                    {/*<NavSearch active={searchActive} close={onSearchClose}/>*/}
                </div>
            </div>
        </Header>
    )
}

PublicHeaderNav.propTypes = {
    signOut: PropTypes.func.isRequired,
}
PublicHeaderNav.defaultProps = {
    signOut: () => {

    }
}

const mapStateToProps = ({theme,auth}) => {
    let { loading, data,isAuthenticated,token } = auth
    const {navCollapsed, navType, headerNavColor, mobileNav, currentTheme, direction} = theme;
    return {
        loading, data,isAuthenticated,token,
        navCollapsed, navType, headerNavColor, mobileNav, currentTheme, direction
    }
};

export default connect(mapStateToProps, {signOut,toggleCollapsedNav, onMobileNavToggle})(PublicHeaderNav);
