import React,{useEffect,useState} from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import AppLayout from "layouts/app-layout";
import AuthLayout from 'layouts/auth-layout';
import AppLocale from "lang";
import { IntlProvider } from "react-intl";
import { ConfigProvider,notification } from 'antd';
import {APP_MY_VILLA_PATH, APP_PREFIX_PATH, AUTH_PREFIX_PATH} from 'configs/AppConfig'
import useBodyClass from 'hooks/useBodyClass';
import PublicLayout from "./public-views/index";
import AdminRoute from "../permissions/AdminRoute";
import {setGeo, userLoaded} from "../redux/actions";
import PropTypes from "prop-types";
import ErrorTwo from "./auth-views/errors/error-page-2";
import UserRoute from "../permissions/UserRoute";
import MyViewsLayout from "./my-views";
import VillaRoute from "../permissions/VillaRoute";

export const Views = (props) => {
  const {setGeo, locale, location,auth, direction } = props;
  let { userLoaded } = props
  const currentAppLocale = AppLocale[locale];
  useBodyClass(`dir-${direction}`);

  useEffect(() => {
    userLoaded()
  }, [userLoaded]);

  const [loc, setLoc] = useState({
    lat: null,
    lng: null
  })
  const [loadingPage, setLoadingPage] = useState(false)

  useEffect(() => {
    setLoadingPage(true)
    const times = setTimeout(() => {
      setLoadingPage(false)
    }, 1000)
    return () => clearTimeout(times)
  }, [setLoadingPage]);

  useEffect(() => {
    if ('geolocation' in navigator) {
      navigator.geolocation.getCurrentPosition(function (position) {
        console.log({position})
        setGeo({lat: position.coords.latitude, long: position.coords.longitude})
        setLoc({lat: position.coords.latitude, lng: position.coords.longitude})
      });
    }
  }, [setLoc,setGeo])

  useEffect(()=> {
    if(auth?.isAuthenticated && typeof(auth?.data) !== "undefined" && auth?.data !== null && typeof(auth?.data) === "object" && Object.keys(auth?.data).length > 0){
      if(typeof(auth?.data?.user) !== "undefined" && auth?.data?.user &&  Object.keys(auth?.data?.user).length > 0){
        if(typeof(auth?.data?.user?.is_verify) !== "undefined" && auth?.data?.user?.is_verify === false){
          notification.warn({
            message: 'Email Unverified',
            description:
                'Please check your email to verify your account.',
            duration: 0,
            placement:"bottomRight"
          })
        }
      }
      // notification.warn()
    }
  },[auth])

  return (
    <IntlProvider
      locale={currentAppLocale.locale}
      messages={currentAppLocale.messages}>
      <ConfigProvider locale={currentAppLocale.antd} direction={direction}>
        <Switch>
          <VillaRoute path={APP_MY_VILLA_PATH}>
            <MyViewsLayout direction={direction} location={location} geo={loc}/>
          </VillaRoute>
          <AdminRoute path={APP_PREFIX_PATH}>
            <AppLayout direction={direction} location={location} geo={loc}/>
          </AdminRoute>


          <Route path={AUTH_PREFIX_PATH}>
            <AuthLayout direction={direction} location={location} geo={loc}/>
          </Route>

          {/*<Route path={'/access-denied'}>*/}
          {/*  <ErrorTwo redirect={'/auth/login'}/>*/}
          {/*</Route>*/}


          <Route path={'/'}>
            <PublicLayout location={location} direction={direction} geo={loc}/>
          </Route>
        </Switch>
      </ConfigProvider>
    </IntlProvider>
  )
}

Views.propTypes = {
  userLoaded: PropTypes.func.isRequired,
  setGeo: PropTypes.func.isRequired,
}

const mapStateToProps = ({ theme, auth }) => {
  const { locale, direction } =  theme;
  const { token } = auth;
  return { locale, token, direction,auth }
};

export default withRouter(connect(mapStateToProps,{userLoaded,setGeo})(Views));
