import React from "react";
import {Menu, Dropdown, Avatar, Button} from "antd";
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import {
    EditOutlined,
    SettingOutlined,
    ShopOutlined,
    QuestionCircleOutlined,
    LogoutOutlined
} from '@ant-design/icons';
import Icon from 'components/util-components/Icon';
import {signOut} from 'redux/actions/Auth';
import Flex from "../shared-components/Flex";
import {Link} from "react-router-dom";


export const NavProfile = (props) => {
    let {signOut, title, subTitle} = props
    let {
        loading, data, isAuthenticated
    } = props


    const profileImg = "/img/avatars/thumb-1.jpg";
    const [menuItem] = React.useState([
        {
            title: "Edit Profile",
            icon: EditOutlined,
            path: "/profile"
        },
        {
            title: "Account Setting",
            icon: SettingOutlined,
            path: "/settings",
        }
    ])

    const profileMenu = (
        <div className="nav-profile nav-dropdown">
            <div className="nav-profile-header">
                <div className="d-flex overflow-hidden text-overflow-ellipsis" style={{
                    textOverflow: "ellipsis"
                }}>
                    <div style={{minWidth: 45}}>
                        <Avatar size={45} src={profileImg}/>
                    </div>
                    <div className="pl-3 overflow-hidden" style={{
                        textOverflow: "ellipsis",
                        whiteSpace: 'nowrap',
                        width: "calc(100% - 45px)",
                    }}>
                        <Link to={'/profile'}><h4 className="mb-0" style={{
                            whiteSpace: "nowrap",
                            textOverflow: "ellipsis"
                        }}>{[data?.user?.firstName, data?.user?.lastName].join(" ") ?? "-"}</h4></Link>
                        <span className="text-muted">{data?.user?.username ?? subTitle ?? "-"}</span>
                    </div>
                </div>
            </div>
            <div className="nav-profile-body">
                <Menu>
                    {menuItem.map((el, i) => {
                        return (
                            <Menu.Item key={i}>
                                <Link to={el.path}>
                                    <Icon type={el.icon}/>
                                    <span className="font-weight-normal">{el.title}</span>
                                </Link>
                            </Menu.Item>
                        );
                    })}
                    <Menu.Item key={menuItem.length + 1} onClick={e => signOut()}>
            <span>
              <LogoutOutlined/>
              <span className="font-weight-normal">Sign Out</span>
            </span>
                    </Menu.Item>
                </Menu>
            </div>
        </div>
    );

    return loading ? "Loading..." :
        isAuthenticated ?
            (
                <Dropdown placement="bottomRight" overlay={profileMenu} trigger={["click"]}>
                    <Menu className="d-flex align-item-center" mode="horizontal">
                        <Menu.Item key="profile">
                            <Avatar src={profileImg}/>
                        </Menu.Item>
                    </Menu>
                </Dropdown>
            ) : (
                <Flex alignItems={'center'} mobileFlex={true}>
                    <Button size={'small'} type={'primary'} href={'/auth/login'}>Sign in</Button>
                    <Button className={'ml-2'} size={'small'} href={'/auth/register'}>Sign Up</Button>
                </Flex>
            )
}

export default connect(({auth}) => {
    let {loading, data, token, isAuthenticated} = auth
    return {loading, data, token, isAuthenticated}
}, {signOut})(NavProfile)
