import {
    DELETE_IMAGE_AT_ROOMS,
    ERROR_DELETE_IMAGE_AT_ROOMS,
    ERROR_GET_QUEUE_ROOMS_LIST_FILTERED,
    ERROR_GET_ROOMS_LISTS,
    ERROR_GET_ROOMS_SINGLE,
    GET_QUEUE_ROOMS_LIST_FILTERED,
    GET_ROOMS_LISTS,
    GET_ROOMS_SINGLE, MY_ROOMS_QUEUE_HISTORY, MY_ROOMS_QUEUE_HISTORY_ERROR, MY_ROOMS_QUEUE_HISTORY_SUCCESS,
    MY_ROOMS_QUEUE_LISTS, MY_ROOMS_QUEUE_LISTS_ERROR, MY_ROOMS_QUEUE_LISTS_SUCCESS,
    SUCCESS_DELETE_IMAGE_AT_ROOMS,
    SUCCESS_GET_QUEUE_ROOMS_LIST_FILTERED,
    SUCCESS_GET_ROOMS_LISTS,
    SUCCESS_GET_ROOMS_SINGLE
} from "../constants";
import Mutate from "../mutations";


export default function(state = Mutate.Rooms.initialState,action){
    let { type } = action
    switch (type){

        case MY_ROOMS_QUEUE_LISTS:
            return Mutate.Rooms.queue.waiting.get(state,action)
        case MY_ROOMS_QUEUE_LISTS_SUCCESS:
            return Mutate.Rooms.queue.waiting.getSuccess(state,action)
        case MY_ROOMS_QUEUE_LISTS_ERROR:
            return Mutate.Rooms.queue.waiting.getError(state,action)

        case MY_ROOMS_QUEUE_HISTORY:
            return Mutate.Rooms.queue.all.get(state,action)
        case MY_ROOMS_QUEUE_HISTORY_SUCCESS:
            return Mutate.Rooms.queue.all.getSuccess(state,action)
        case MY_ROOMS_QUEUE_HISTORY_ERROR:
            return Mutate.Rooms.queue.all.getError(state,action)

        case GET_QUEUE_ROOMS_LIST_FILTERED:
            return  Mutate.Rooms.search.get(state,action)
        case SUCCESS_GET_QUEUE_ROOMS_LIST_FILTERED:
            return  Mutate.Rooms.search.success(state,action)
        case ERROR_GET_QUEUE_ROOMS_LIST_FILTERED:
            return  Mutate.Rooms.search.error(state,action)

        case GET_ROOMS_SINGLE:
            return Mutate.Rooms.single.get(state,action)
        case SUCCESS_GET_ROOMS_SINGLE:
            return Mutate.Rooms.single.getSuccess(state,action)
        case ERROR_GET_ROOMS_SINGLE:
            return Mutate.Rooms.single.getError(state,action)

        case DELETE_IMAGE_AT_ROOMS:
            return Mutate.Rooms.single.image.delete(state,action)
        case SUCCESS_DELETE_IMAGE_AT_ROOMS:
            return Mutate.Rooms.single.image.deleteSuccess(state,action)
        case ERROR_DELETE_IMAGE_AT_ROOMS:
            return Mutate.Rooms.single.image.deleteError(state,action)

        case GET_ROOMS_LISTS:
            return Mutate.Rooms.rooms.dynamic.get(state,action)
        case SUCCESS_GET_ROOMS_LISTS:
            return Mutate.Rooms.rooms.dynamic.getSuccess(state,action)
        case ERROR_GET_ROOMS_LISTS:
            return Mutate.Rooms.rooms.dynamic.getError(state,action)
        default:
            return state;
    }
}
