import { all, takeEvery, put, fork, call } from 'redux-saga/effects';
import {
    ADD_NEW_CATEGORY,
    ADMIN_ERROR_GET_CATEGORY_LIST,
    ADMIN_ERROR_GET_TRAVEL_LISTS,
    ADMIN_GET_CATEGORY_LIST,
    ADMIN_GET_TRAVEL_LISTS,
    ADMIN_SUCCESS_GET_CATEGORY_LIST,
    ADMIN_SUCCESS_GET_TRAVEL_LISTS, ERROR_ADD_NEW_CATEGORY,
    ERROR_GET_ALL_CATEGORIES,
    GET_ALL_CATEGORIES,
    getApiRoute,
    PUBLIC_ERROR_GET_TRAVEL_NEARBY, PUBLIC_ERROR_GET_TRAVEL_SINGLE,
    PUBLIC_GET_TRAVEL_NEARBY, PUBLIC_GET_TRAVEL_SINGLE,
    PUBLIC_SUCCESS_GET_TRAVEL_NEARBY, PUBLIC_SUCCESS_GET_TRAVEL_SINGLE,
    SUCCESS_ADD_NEW_CATEGORY, SUCCESS_GET_ALL_CATEGORIES
} from "../constants";
import ApiService from "../../services/Api.service";
import {calling} from "./default.saga";



export function* _adminGetLists (){
    yield takeEvery(ADMIN_GET_TRAVEL_LISTS, function*({query}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:getApiRoute('admin/travel/list'),
                            config:{
                                params: query
                            }
                        }).get()
                    )
                })
            })
            if(!response?.error){
                yield put({
                    type:ADMIN_SUCCESS_GET_TRAVEL_LISTS,
                    ...response
                })
            }else{
                yield put ({
                    type: ADMIN_ERROR_GET_TRAVEL_LISTS,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put ({
                type: ADMIN_ERROR_GET_TRAVEL_LISTS,
                message: err?.message ?? "Some Error"
            })
        }
    })
}

export function* _catList() {
    yield takeEvery(ADMIN_GET_CATEGORY_LIST, function* ({query}) {
        try {
            const response = yield call(() => {
                return new Promise(resolve => {
                    resolve(new ApiService({
                        url: getApiRoute('admin/travel/category/list'),
                        config: {
                            params: query
                        },
                    }).get())
                })
            })

            if(!response?.error){
                yield put ({
                    type:ADMIN_SUCCESS_GET_CATEGORY_LIST,
                    ...response
                })
            }else{
                yield put({
                    type: ADMIN_ERROR_GET_CATEGORY_LIST,
                    message: response?.message ?? "Some Error"
                })
            }


        } catch (err) {
            yield put({
                type: ADMIN_ERROR_GET_CATEGORY_LIST,
                message: err.message
            })
        }
    })
}


