export function* _byNearby(){
    yield takeEvery(PUBLIC_GET_TRAVEL_NEARBY, function*({coordinates,params}){
        try{
            const formdata = new URLSearchParams()
            formdata.append('lat',coordinates?.lat ?? null)
            formdata.append('lng',coordinates?.long ?? null)
            formdata.append('max',coordinates?.max ?? 1000000)


            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(new ApiService({
                        url:"/api/v1/travel/list/near-me",
                        body:formdata,
                        config:{
                            params: {
                                ...params
                            }
                        }
                    }).post())
                })
            })

            if(!response?.error){
                yield put ({
                    type:PUBLIC_SUCCESS_GET_TRAVEL_NEARBY,
                    ...response,
                })
            }else{
                yield put ({
                    type:PUBLIC_ERROR_GET_TRAVEL_NEARBY,
                    message: response?.message ?? "Some Error",
                })
            }

        }catch(err){
            yield put ({
                type:PUBLIC_ERROR_GET_TRAVEL_NEARBY,
                message: err.message,
            })
        }
    })
}



export function* _addNewCategory(){
    yield takeEvery(ADD_NEW_CATEGORY, function*({formdata}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:"/api/v1/category",
                            body:formdata
                        }).post()
                    )
                })
            })
            if(!response?.error){
                yield all([
                    put({
                        type:SUCCESS_ADD_NEW_CATEGORY,
                        ...response
                    })
                ])
            }else{
                yield all([
                    put({
                        type:ERROR_ADD_NEW_CATEGORY,
                        message: response?.message ?? "Some Error"
                    })
                ])
            }
        }catch(err){
            yield put({
                type:ERROR_GET_ALL_CATEGORIES,
                message: err?.message ?? "Some Error"
            })
        }
    })
}
export function* _getAllCategory(){
    yield takeEvery(GET_ALL_CATEGORIES, function*({params}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:"/api/v1/category",
                            config:{
                                params
                            }
                        }).get()
                    )
                })
            })

            if(!response?.error){
                yield put({
                    type:SUCCESS_GET_ALL_CATEGORIES,
                    ...response
                })
            }else{
                yield put({
                    type:ERROR_GET_ALL_CATEGORIES,
                    message: response?.message ?? "Some Error"
                })
            }

        }catch(err){
            yield put({
                type:ERROR_GET_ALL_CATEGORIES,
                message: err?.message ?? "Some Error"
            })
        }
    })
}

export function* _getPublicTravel(){
    yield takeEvery(PUBLIC_GET_TRAVEL_SINGLE, function*({id,params}){
        try{

            let response = yield calling({url:`/api/v1/travel/show/${id}`,config:{params}},'get')

            if(!response?.error){
                yield put({
                    type:PUBLIC_SUCCESS_GET_TRAVEL_SINGLE,
                    ...response
                })
            }else{
                yield put({
                    type:PUBLIC_ERROR_GET_TRAVEL_SINGLE,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put({
                type:PUBLIC_ERROR_GET_TRAVEL_SINGLE,
                message: err?.message ?? null
            })
        }
    })
}


export default function* rootSaga() {
    yield all([
        fork(_adminGetLists),
        fork(_catList),
        fork(_byNearby),
        fork(_getAllCategory),
        fork(_addNewCategory),
        fork(_getPublicTravel),
    ])
}
