import React, {useEffect, useState} from 'react'
import {connect} from 'react-redux'
import {Redirect, Route} from 'react-router-dom'
import {createBrowserHistory} from "history";
import Loading from "../components/shared-components/Loading";


const VillaRoute = ({component: Component, isAuthenticated, token, data, loading, ...rest}) => {


    return (
        <Route
            {...rest}
            history={createBrowserHistory}
            render={(props) => {
                return !loading ?
                    isAuthenticated && token !== null ? (
                        typeof (data?.user?.role) !== "undefined" &&
                        data?.user?.role === "villa" ? (
                            <Component {...props}/>
                        ) : (
                            <Component {...props}/>
                            // <Redirect to={{
                            //     pathname: "/access-denied",
                            //     state: {redirect: rest.location.pathname}
                            // }}/>
                        )

                    ) : (
                        <Component {...props}/>
                        // <Loading cover={'content'}/>
                        // <Redirect to={{
                        //     pathname: "/auth/login",
                        //     state: {redirect: rest.location.pathname}
                        // }}/>
                    ) : (
                        <Loading cover={'content'}/>
                    )
            }}

        />
    )
}

const mapStateToProps = ({auth}) => {

    const {isAuthenticated, loading, data} = auth
    return {isAuthenticated, loading, data}
}

export default connect(mapStateToProps, {})(React.memo(VillaRoute))
