const initialState = {
    loading: true,
    message: null,
    type: null,
    error: false,
    data: null,
    selected: [],
    province:{
        loading_more:false,
        loading: true,
        message: null,
        error:false,
        type: null,
        params:{
            page:1,
            limit:10,
        },
        pagination: {
            current_page:1,
            total_page:0,
            total_record:0
        },
        data: []
    },
    regencies:{
        loading: true,
        message: null,
        error:false,
        params:{
            page:1,
            limit:10,
        },
        pagination: {
            current_page:1,
            total_page:0,
            total_record:0
        },
        data: []
    },
    districts:{
        loading: true,
        message: null,
        error:false,
        params:{
            page:1,
            limit:10,
        },
        pagination: {
            current_page:1,
            total_page:0,
            total_record:0
        },
        data: []
    },
    vilages:{
        loading: true,
        message: null,
        error:false,
        params:{
            page:1,
            limit:10,
        },
        pagination: {
            current_page:1,
            total_page:0,
            total_record:0
        },
        data: []
    },
    collections: {
        districts: {
            loading: true,
            message: null,
            type: null,
            error: false,
            data: []
        }
    }
}
const Mutations = {
    districts: {
        karawang: {
            get: function (state, action) {
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        districts: {
                            ...state.collections.districts,
                            loading:true,
                            message: null,
                            error:false,
                            type:action.type ?? null,
                            data: []
                        }
                    }
                }
            },
            success: function(state,action){
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        districts: {
                            ...state.collections.districts,
                            loading:false,
                            message: action?.message ?? null ,
                            error:false,
                            type:action.type ?? null,
                            data: [...action?.data]
                        }
                    }
                }
            },
            error: function(state,action){
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        districts: {
                            ...state.collections.districts,
                            loading: false,
                            message: action?.message ?? null,
                            type: action?.type,
                            error: true,
                            data: []
                        }
                    }
                }
            }
        }
    }
}

export const Locations = {
    initialState,
    ...Mutations

}
