import {
    ADMIN_ERROR_GET_VILLA_LISTS,
    ADMIN_GET_VILLA_LISTS,
    ADMIN_SEARCH_VILLA,
    ADMIN_SUCCESS_GET_VILLA_LISTS,
    GET_MY_VILLA_PROFILE,
    GET_MY_VILLA_PROFILE_ERROR,
    GET_MY_VILLA_PROFILE_SUCCESS,
    GET_SINGLE_VILLA, GET_SINGLE_VILLA_ERROR, GET_SINGLE_VILLA_SUCCESS,
    GET_VILLA_LIST,
    GET_VILLA_LIST_ERROR,
    GET_VILLA_LIST_SUCCESS,
    initialPagination,
    initialParams
} from "../constants";
import Mutate from "../mutations";


const initialState = {
    admin: {
        search:{
            keyword: null,
        },
        list: {
            loading: true,
            error:false,
            message: null,
            type: null,
            pagination :initialPagination,
            params: initialParams,
            data: []
        }
    },
    detail: {
        loading:true,
        message: null,
        error:false,
        type: null,
        data:null
    },
    list :{
        loading:true,
        message: null,
        error:false,
        pagination : initialPagination,
        params: initialParams,
        data: []
    }
}

export default function(state = initialState , action){
    switch (action?.type){
        case ADMIN_GET_VILLA_LISTS:
            return Mutate.Villa.admin.list.get(state,action)
        case ADMIN_SUCCESS_GET_VILLA_LISTS:
            return Mutate.Villa.admin.list.success(state,action)
        case ADMIN_ERROR_GET_VILLA_LISTS:
            return Mutate.Villa.admin.list.error(state,action)
        case ADMIN_SEARCH_VILLA:
            return Mutate.Villa.admin.list.get(state,action)


        case GET_VILLA_LIST:
            return {
                ...state,
                list: {
                    ...state.list,
                    loading:true,
                    message: null,
                    error:false,
                    data: []
                }
            }
        case GET_VILLA_LIST_SUCCESS:
            return {
                ...state,
                list: {
                    ...state.list,
                    loading:false,
                    message: action?.message ?? null,
                    error:false,
                    pagination: {
                        ...state.list.pagination,
                        ...action?.pagination
                    },
                    params: {
                        ...state.list.params,
                        ...action?.params
                    },
                    data: [
                        ...action?.data
                    ]
                }
            }
        case GET_VILLA_LIST_ERROR:
            return {
                ...state,
                list: {
                    ...state.list,
                    loading:false,
                    message: action?.message ?? null,
                    error:true,
                    data: []
                }
            }


        case GET_MY_VILLA_PROFILE:
            return Mutate.My.mutate.detail.get(state,action)
        case GET_MY_VILLA_PROFILE_SUCCESS:
            return Mutate.My.mutate.detail.getSuccess(state,action)
        case GET_MY_VILLA_PROFILE_ERROR:
            return Mutate.My.mutate.detail.getError(state,action)


        case GET_SINGLE_VILLA:
            return {
                ...state,
                detail:{
                    ...state.detail,
                    loading:true,
                    message: null,
                    error:false,
                    type: action?.type,
                    data: null
                }
            }
        case GET_SINGLE_VILLA_SUCCESS:
            return {
                ...state,
                detail:{
                    ...state.detail,
                    loading:false,
                    message: action?.message ?? null,
                    error:false,
                    type: action?.type ?? null,
                    data: action?.data ?? null
                }
            }
        case GET_SINGLE_VILLA_ERROR:
            return {
                ...state,
                detail:{
                    ...state.detail,
                    loading:false,
                    message: action?.message ?? null,
                    error:true,
                    type: action?.type ?? null,
                    data: action?.data ?? null
                }
            }

        default:
            return state
    }
}
