import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from 'components/shared-components/Loading';
import { APP_PREFIX_PATH } from 'configs/AppConfig'
import NotFound from "../NotFound";
import UserRoute from "../../permissions/UserRoute";
import {PrivateRoute} from "../../permissions/PrivateRoute";

export const Views = () => {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <UserRoute path={`/villa`} component={lazy(() => import(`./villa/index`))} />
                <UserRoute path={'/travel'} component={lazy(()=> import("./travel/index"))}/>
                <UserRoute path={'/settings'} component={lazy(()=> import("./settings/index"))}/>
                <UserRoute path={'/room'} component={lazy(()=> import("./rooms/index"))}/>
                <UserRoute path={'/transaction'} component={lazy(()=> import("./transaction/index"))}/>
                <Route path={`/book`} component={lazy(()=> import('./booking/booking-view'))}/>
                <UserRoute path={'/'} exact component={lazy(()=> import("./home/index"))}/>
                <Route component={lazy(()=> import("../NotFound"))}/>
            </Switch>
        </Suspense>
    )
}

export default React.memo(Views);
