    ERROR_GET_PROVINCES,
    ERROR_GET_REGENCIES, ERROR_GET_VILAGES, ERROR_LOAD_MORE_GET_DISTRICTS,
    ERROR_LOAD_MORE_GET_PROVINCES,
    ERROR_LOAD_MORE_GET_REGENCIES, ERROR_LOAD_MORE_GET_VILAGES,
    GET_DISTRICTS,
    GET_PROVINCES,
    GET_REGENCIES, GET_VILAGES, LOAD_MORE_GET_DISTRICTS,
