/**
 * GET PROVINCES
 * @param params
 * @returns {{type: string, params: {}}}
 * @constructor
 */
export const GetProvinces = ({params})=> ({
    type:GET_PROVINCES,
    params
})
export const SuccessGetProvinces = ({data,message,error,pagination,params})=> ({
    type:SUCCESS_GET_PROVINCES,
    data,message,error,pagination,params
})
export const ErrorGetProvinces = ({message})=> ({
    type:ERROR_GET_PROVINCES,
    message
})
export const LoadMoreGetProvinces = ({params})=> ({
    type:LOAD_MORE_GET_PROVINCES,
    params,
})
export const SuccessLoadMoreProvinces = ({data,message,error,pagination,params})=> ({
    type:SUCCESS_LOAD_MORE_GET_PROVINCES,
    data,message,error,pagination,params
})
export const ErrorLoadMoreProvinces = ({message})=> ({type:ERROR_LOAD_MORE_GET_PROVINCES,message})

