import {all, takeEvery, put, fork, call} from 'redux-saga/effects';
import {
    ERROR_GET_DISTRICTS_LISTS_OF_KARAWANG, GET_DISTRICTS,
    GET_DISTRICTS_LISTS_OF_KARAWANG, GET_PROVINCES, GET_REGENCIES, GET_VILAGES,
    SUCCESS_GET_DISTRICTS_LISTS_OF_KARAWANG
} from "../constants";
import ApiService from "../../services/Api.service";
import {
    ErrorGetDistricts,
    ErrorGetProvinces, ErrorGetVillages,
    SuccessGetDistricts,
    SuccessGetProvinces,
    SuccessGetRegencies, SuccessGetVillages
} from "../actions/Locations";


export function* _getDistrictsOfKarawang() {
    yield takeEvery(GET_DISTRICTS_LISTS_OF_KARAWANG, function* ({}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(new ApiService({
                        url: "/api/v1/locations/district/karawang"
                    }).get())
                })
            })

            if (!response?.error) {
                yield put({
                    type: SUCCESS_GET_DISTRICTS_LISTS_OF_KARAWANG,
                    ...response
                })
            } else {
                yield put({
                    type: ERROR_GET_DISTRICTS_LISTS_OF_KARAWANG,
                })
            }
        } catch (err) {
            yield put({
                type: ERROR_GET_DISTRICTS_LISTS_OF_KARAWANG,
            })
        }
    })
}


export function* getProvince() {
    yield takeEvery(GET_PROVINCES, function* ({params}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(
                        new ApiService({
                            url: "/api/v1/locations/province",
                            config: {
                                params,
                            }
                        }).get())
                })
            })
            if (!response?.error) {
                yield put(SuccessGetProvinces({...response}))
            } else {
                yield put(ErrorGetProvinces({message: response?.message ?? "Some Error"}))
            }
        } catch (err) {
            yield put(ErrorGetProvinces({message: err?.message ?? "Some Error"}))
        }
    })
}

export function* getRegencies() {
    yield takeEvery(GET_REGENCIES, function* ({params}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(
                        new ApiService({
                            url: "/api/v1/locations/regencies",
                            config: {
                                params,
                            }
                        }).get()
                    )
                })
            })
            if (!response?.error) {
                yield put(SuccessGetRegencies({...response}));
            } else {
                yield put(ErrorGetDistricts({message: response?.message ?? "Some Error"}))
            }
        } catch (err) {
            yield put(ErrorGetDistricts({message: err?.message ?? "Some Error"}))
        }
    })
}

export function* getDistricts() {
    yield takeEvery(GET_DISTRICTS, function* ({params}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(
                        new ApiService({
                            url: "/api/v1/locations/district",
                            config: {
                                params,
                            }
                        }).get()
                    )
                })
            })
            if (!response?.error) {
                yield put(SuccessGetDistricts({
                    data: response?.data ?? [],
                    message: response?.message ?? null,
                    pagination: response?.pagination ?? {},
                    params: response?.params ?? {},
                    error: response?.error ?? false
                }))
            } else {
                yield put(ErrorGetDistricts({message: response?.message ?? "Some Error"}))
            }
        } catch (err) {
            yield put(ErrorGetDistricts({message: err?.message ?? "Some Error"}))
        }
    })
}

export function* getVilages() {
    yield takeEvery(GET_VILAGES, function* ({params}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(
                        new ApiService({
                            url: "/api/v1/locations/villages",
                            config: {
                                params,
                            }
                        }).get()
                    )
                })
            })
            if (!response?.error) {
                yield put(SuccessGetVillages({...response}))
            } else {
                yield put(ErrorGetVillages({message: response?.message ?? "Some Error"}))
            }
        } catch (err) {
            yield put(ErrorGetVillages({message: err?.message ?? "Some Error"}))
        }
    })
}

export default function* rootSaga() {
    yield all([
        fork(_getDistrictsOfKarawang),
        fork(getProvince),
        fork(getVilages),
        fork(getDistricts),
        fork(getRegencies),
    ])
}
