import {
    PUBLIC_BOOKED_ROOMS,
    PUBLIC_BOOKED_ROOMS_ERROR,
    PUBLIC_BOOKED_ROOMS_SUCCESS,
    PUBLIC_BOOKING_MY_DETAIL,
    PUBLIC_BOOKING_MY_DETAIL_ERROR,
    PUBLIC_BOOKING_MY_DETAIL_SUCCESS,
    PUBLIC_BOOKING_MY_LISTS,
    PUBLIC_BOOKING_MY_LISTS_ERROR,
    PUBLIC_BOOKING_MY_LISTS_SUCCESS
} from "../constants";
import Mutate from "../mutations";

const initialState = {
    loading:true,
    error:false,
    message: null,
    type: null,
    params : {},
    data: null,


    collections : {
        now: {
            loading:true,
            error:false,
            message: null,
            type: null,
            params : {},
            data: null,
        },
        lists: {
            loading:true,
            error:false,
            message: null,
            type: null,
            params : {
                page:1,
                limit:10,
            },
            pagination : {
                page:1,
                limit:10
            },
            data: []
        }
    },

}

export default function(state = initialState, action){
    switch (action?.type){
        /**
         * @access - [ user / public ]
         */
        case PUBLIC_BOOKED_ROOMS:
            return Mutate.Booking.mutate.public.create(state,action)
        case PUBLIC_BOOKED_ROOMS_SUCCESS :
            return Mutate.Booking.mutate.public.createSuccess(state,action)
        case PUBLIC_BOOKED_ROOMS_ERROR:
            return Mutate.Booking.mutate.public.createError(state,action)


        case PUBLIC_BOOKING_MY_LISTS:
            return Mutate.Booking.mutate.public.lists.get(state,action)
        case PUBLIC_BOOKING_MY_LISTS_SUCCESS:
            return Mutate.Booking.mutate.public.lists.getSuccess(state,action)
        case PUBLIC_BOOKING_MY_LISTS_ERROR:
            return Mutate.Booking.mutate.public.lists.getError(state,action)

        case PUBLIC_BOOKING_MY_DETAIL:
            return Mutate.Booking.mutate.public.detail.get(state,action)
        case PUBLIC_BOOKING_MY_DETAIL_SUCCESS:
            return Mutate.Booking.mutate.public.detail.getSuccess(state,action)
        case PUBLIC_BOOKING_MY_DETAIL_ERROR:
            return Mutate.Booking.mutate.public.detail.getError(state,action)


        default:
            return state
    }
}