import {
    ADD_NEW_CATEGORY,
    ADMIN_ERROR_GET_CATEGORY_LIST,
    ADMIN_ERROR_GET_TRAVEL_LISTS,
    ADMIN_GET_CATEGORY_LIST,
    ADMIN_GET_TRAVEL_LISTS,
    ADMIN_SUCCESS_GET_CATEGORY_LIST,
    ADMIN_SUCCESS_GET_TRAVEL_LISTS,
    ERROR_ADD_NEW_CATEGORY,
    ERROR_GET_ALL_CATEGORIES,
    GET_ALL_CATEGORIES,
    GET_DETAIL_CATEGORY,
    PUBLIC_ERROR_GET_TRAVEL_NEARBY, PUBLIC_ERROR_GET_TRAVEL_SINGLE,
    PUBLIC_GET_TRAVEL_NEARBY, PUBLIC_GET_TRAVEL_SINGLE,
    PUBLIC_SUCCESS_GET_TRAVEL_NEARBY, PUBLIC_SUCCESS_GET_TRAVEL_SINGLE,
    SUCCESS_ADD_NEW_CATEGORY,
    SUCCESS_GET_ALL_CATEGORIES,
} from "../constants";
import Mutate from "../mutations";
export default function (state = Mutate.Travel.initialState , action){
    switch (action?.type){
        case ADMIN_GET_TRAVEL_LISTS:
            return Mutate.Travel.admin.list.get(state,action)
        case ADMIN_SUCCESS_GET_TRAVEL_LISTS:
            return Mutate.Travel.admin.list.success(state,action)
        case ADMIN_ERROR_GET_TRAVEL_LISTS:
            return Mutate.Travel.admin.list.error(state,action)

        case ADMIN_GET_CATEGORY_LIST:
            return Mutate.Travel.admin.category.list.get(state,action)
        case ADMIN_SUCCESS_GET_CATEGORY_LIST:
            return Mutate.Travel.admin.category.list.success(state,action)
        case ADMIN_ERROR_GET_CATEGORY_LIST:
            return Mutate.Travel.admin.category.list.error(state,action)

        case PUBLIC_GET_TRAVEL_NEARBY:
            return Mutate.Travel.public.nearby.get(state,action)
        case PUBLIC_SUCCESS_GET_TRAVEL_NEARBY:
            return Mutate.Travel.public.nearby.success(state,action)
        case PUBLIC_ERROR_GET_TRAVEL_NEARBY:
            return Mutate.Travel.public.nearby.error(state,action)


        case PUBLIC_GET_TRAVEL_SINGLE:
            return Mutate.Travel.public.single.get(state,action)
        case PUBLIC_SUCCESS_GET_TRAVEL_SINGLE:
            return Mutate.Travel.public.single.success(state,action)
        case PUBLIC_ERROR_GET_TRAVEL_SINGLE:
            return Mutate.Travel.public.single.error(state,action)
        
        case ADD_NEW_CATEGORY:
            return Mutate.Travel.admin.category.added.post(state,action)
        case SUCCESS_ADD_NEW_CATEGORY:
            return Mutate.Travel.admin.category.added.success(state,action)
        case ERROR_ADD_NEW_CATEGORY:
            return Mutate.Travel.admin.category.added.error(state,action)

        case GET_ALL_CATEGORIES:
            return Mutate.Travel.admin.category.lists.get(state,action)
        case SUCCESS_GET_ALL_CATEGORIES:
            return Mutate.Travel.admin.category.lists.success(state,action)
        case ERROR_GET_ALL_CATEGORIES:
            return Mutate.Travel.admin.category.lists.error(state,action)
        default:
            return state
    }
}
