export function setDataDefault(data = [],arr = []){
    if(typeof(data) !== "undefined" && Array.isArray(data) && data.length > 0){
        for(let i = 0 ; i < data.length ; i++){
            arr.push({
                ...data[i],
                loading:false,
                message: null,
                error:false
            })
        }
    }
}
