import {
    ADMIN_DELETE_CATEGORY_BY_ID,
    ADMIN_GET_CATEGORY_LIST,
    ADMIN_GET_CATEGORY_SINGLE,
    ADMIN_GET_TRAVEL_LISTS, ADMIN_PUT_CATEGORY_SINGLE
} from "../../constants";

export const _adminGetTravelList = (query = {})=> ({
    type: ADMIN_GET_TRAVEL_LISTS,
    query
})

export const _adminGetTravelCategoryList = (query = {})=> ({
    type: ADMIN_GET_CATEGORY_LIST,
    query
})

export const _adminGetCatSingle = ( id )=> ({
    type:ADMIN_GET_CATEGORY_SINGLE,
    id
})

export const _adminDelCatSingle = ( id )=> ({
    type: ADMIN_DELETE_CATEGORY_BY_ID,
    id
})

export const _adminPutCatSingle = ( field )=> ({
    field,
    type:ADMIN_PUT_CATEGORY_SINGLE
})
