import {all, takeEvery, put, fork, call} from 'redux-saga/effects';
import {
    ADMIN_ERROR_GET_VILLA_LISTS,
    ADMIN_GET_VILLA_LISTS,
    ADMIN_SUCCESS_GET_VILLA_LISTS,
    GET_SINGLE_VILLA, GET_SINGLE_VILLA_ERROR,
    GET_SINGLE_VILLA_SUCCESS,
    GET_VILLA_LIST,
    GET_VILLA_LIST_ERROR,
    GET_VILLA_LIST_SUCCESS,
    getApiRoute
} from "../constants";
import ApiService from "../../services/Api.service";

export function* _adminGetLists() {
    yield takeEvery(ADMIN_GET_VILLA_LISTS, function* ({query}) {
        try {
            const response = yield call(() => {
                return new Promise((resolve) => {
                    resolve(
                        new ApiService({
                            url: getApiRoute('admin/villa/list'),
                            config: {
                                params: query
                            }
                        }).get()
                    )
                })
            })
            if (!response?.error) {
                yield put({
                    type: ADMIN_SUCCESS_GET_VILLA_LISTS,
                    ...response
                })
            } else {
                yield put({
                    type: ADMIN_ERROR_GET_VILLA_LISTS,
                    message: response?.message ?? "Some Error"
                })
            }
        } catch (err) {
            yield put({
                type: ADMIN_ERROR_GET_VILLA_LISTS,
                message: err?.message ?? "Some Error"
            })
        }
    })
}
export function* _publicVillaList(){
    yield takeEvery(GET_VILLA_LIST, function*({params}){
        try{
            const response = yield call (()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url: "/api/v1/villa/list-detail",
                            config: {
                                params: {
                                    ...params,
                                }
                            }
                        }).get()
                    )
                })
            })
            if(!response?.error){
                yield put({
                    type: GET_VILLA_LIST_SUCCESS,
                    ...response
                })
            }else{
                yield put({
                    type:GET_VILLA_LIST_ERROR,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch (err){
            yield put({
                type:GET_VILLA_LIST_ERROR,
                message: err?.message ?? "Some Error"
            })
        }
    })
}


export function* _singleVilla(){
    yield takeEvery(GET_SINGLE_VILLA, function*({id, params = {}}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:`/api/v1/villa/detail/${id}`,
                            config:{
                                params,
                            }
                        }).get()
                    )
                })
            })

            if(!response?.error){
                yield put({
                    type:GET_SINGLE_VILLA_SUCCESS,
                    ...response
                })
            }else{
                yield put({
                    type:GET_SINGLE_VILLA_ERROR,
                    message: response?.message ?? "Some error"
                })
            }

        }catch(err){
            yield put({
                type:GET_SINGLE_VILLA_ERROR,
                message: err?.message ?? "Some error"
            })
        }
    })
}

export default function* rootSaga() {
    yield all([
        fork(_adminGetLists),
        fork(_singleVilla),
        fork(_publicVillaList)
    ])
}
