import {initialPagination, initialParams} from "../constants";

export const villa = {
    admin :{
        search :{
            get: function (state,action){
                return {
                    ...state,
                    admin: {
                        ...state.admin,
                        search: {
                            ...state.admin.search,
                            keyword: action?.keyword ?? null,
                        },
                        list: {
                            ...state.admin.list,
                            loading:true,
                            error:false,
                            type:action?.type ?? null,
                            message: action?.message ?? null,
                            params: {
                                ...state.admin.list.params,
                                ...action?.query
                            },
                            data : []
                        }
                    }
                }
            }
        },
        list :{
            get: function (state,action){
                return {
                    ...state,
                    admin: {
                        ...state.admin,
                        list: {
                            ...state.admin.list,
                            loading:true,
                            error:false,
                            type:action?.type ?? null,
                            message: action?.message ?? null,
                            params: {
                                ...state.admin.list.params,
                                ...action?.query
                            },
                            data : []
                        }
                    }
                }
            },
            success: function(state,action){
                if(typeof(action?.data) !== "undefined" && Array.isArray(action?.data) && action?.data.length > 0){
                    for(let i = 0 ; i < action?.data.length ; i++){
                        state.admin.list.data.push({
                            ...action?.data[i],
                            loading:false,
                            message: null,
                            error:false
                        })
                    }
                }
                return {
                    ...state,
                    admin: {
                        ...state.admin,
                        list: {
                            ...state.admin.list,
                            loading:false,
                            error:false,
                            type:action?.type ?? null,
                            message: action?.message ?? null,
                            pagination: {
                                ...state.admin.list.pagination,
                                ...action?.pagination
                            },
                            params: {
                                ...state.admin.list.params,
                                ...action?.query
                            },
                            data : [
                                ...state.admin.list.data
                            ]
                        }
                    }
                }
            },
            error: function (state,action){
                return {
                    ...state,
                    admin: {
                        ...state.admin,
                        list: {
                            ...state.admin.list,
                            loading:false,
                            error:false,
                            type:action?.type ?? null,
                            message: action?.message ?? null,
                            pagination: initialPagination,
                            params: initialParams,
                            data : []
                        }
                    }
                }
            }
        }
    }
}
