import {all, takeEvery, put, fork, call} from 'redux-saga/effects';
import ApiService from "../../../services/Api.service";
import {
    DELETE_IMAGE_AT_ROOMS,
    ERROR_DELETE_IMAGE_AT_ROOMS,
    GET_ROOMS_LISTS,
    ERROR_GET_QUEUE_ROOMS_LIST_FILTERED,
    ERROR_GET_ROOMS_LISTS,
    ERROR_GET_ROOMS_SINGLE,
    GET_QUEUE_ROOMS_LIST_FILTERED,
    GET_ROOMS_SINGLE,
    SUCCESS_DELETE_IMAGE_AT_ROOMS,
    SUCCESS_GET_QUEUE_ROOMS_LIST_FILTERED,
    SUCCESS_GET_ROOMS_LISTS,
    SUCCESS_GET_ROOMS_SINGLE,
    MY_ROOMS_QUEUE_LISTS,
    MY_ROOMS_QUEUE_HISTORY,
    MY_ROOMS_QUEUE_HISTORY_SUCCESS,
    MY_ROOMS_QUEUE_HISTORY_ERROR,
    MY_ROOMS_QUEUE_LISTS_SUCCESS, MY_ROOMS_QUEUE_LISTS_ERROR
} from "../../constants";
import {calling} from "../default.saga";


export function* _getQueueRoomListsFiltered(){
    yield takeEvery(GET_QUEUE_ROOMS_LIST_FILTERED, function*({query}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(new ApiService({
                        url:"/api/v1/rooms",
                        config: {
                            params:query
                        }
                    }).get())
                })
            })

            if(!response?.error){
                yield put ({
                    type: SUCCESS_GET_QUEUE_ROOMS_LIST_FILTERED,
                    ...response
                })
            }
            else{
                yield put({
                    type:ERROR_GET_QUEUE_ROOMS_LIST_FILTERED,
                })
            }
        }catch(err){
            yield put({
                type:ERROR_GET_QUEUE_ROOMS_LIST_FILTERED,
            })
        }
    })
}

export function* _getRoomDetail(){
    yield takeEvery(GET_ROOMS_SINGLE, function*({id,params,self = '/self'}) {
        try {
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:`/api/v1/rooms${self}/list/${id}`,
                            config:{
                                params: {
                                    ...params
                                }
                            }
                        }).get()
                    )
                })
            })

            if(!response?.error) {
                yield put({
                    type: SUCCESS_GET_ROOMS_SINGLE,
                    ...response
                })
            }else {
                yield put({
                    type: ERROR_GET_ROOMS_SINGLE,
                    message: response?.message ?? "Some Error"
                })
            }

        } catch (err) {
            yield put({
                type:ERROR_GET_ROOMS_SINGLE,
                message: err.message ?? "Some Error"
            })
        }
    })
}

export function* _deleteImageInRoom(){
    yield takeEvery(DELETE_IMAGE_AT_ROOMS, function*({id,imageId}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:`/api/v1/villa/self/rooms/delete/photo/${id}/${imageId}`,
                        }).delete()
                    )
                })
            })
            if(!response?.error){
                yield all([
                    put({
                        type:SUCCESS_DELETE_IMAGE_AT_ROOMS,
                        id,imageId
                    })
                ])
            }else{
                yield put ({
                    type:ERROR_DELETE_IMAGE_AT_ROOMS,
                    imageId,id
                })
            }

        }catch(err){
            yield put ({
                type:ERROR_DELETE_IMAGE_AT_ROOMS,
                imageId,id
            })
        }
    })
}

export function* _roomLists(){
    yield takeEvery(GET_ROOMS_LISTS, function*({params}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:"/api/v1/rooms",
                            config: {
                                params
                            }
                        }).get()
                    )
                })
            })

            console.log({response})
            if(!response?.error){
                yield put ({
                    type: SUCCESS_GET_ROOMS_LISTS,
                    ...response
                })
            }else{
                yield put({
                    type:ERROR_GET_ROOMS_LISTS,
                    message: response?.message ?? "Some error"
                })
            }

        }catch(err){
            yield put({
                type:ERROR_GET_ROOMS_LISTS,
                message: err.message ?? "Some error"
            })
        }
    })
}


export function* _getMyRoomQueue(){
    yield takeEvery(MY_ROOMS_QUEUE_LISTS, function*({params}){
        try{
            const response = yield calling({url:"/api/v1/queue-room",config: {params}},'get')
            if(!response?.error){
                yield put({
                    type:MY_ROOMS_QUEUE_LISTS_SUCCESS,
                    ...response
                })
            }else{
                yield put({
                    type:MY_ROOMS_QUEUE_LISTS_ERROR,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put({
                type: MY_ROOMS_QUEUE_LISTS_ERROR,
                message: err?.message
            })
        }
    })
}
export function* _getMyRoomQueueHistory(){
    yield takeEvery(MY_ROOMS_QUEUE_HISTORY, function*({params}){
        try{
            const response = yield calling({url:"/api/v1/queue-room",config: {params}},'get')
            if(!response?.error){
                yield put({
                    type:MY_ROOMS_QUEUE_HISTORY_SUCCESS,
                    ...response
                })
            }else{
                yield put({
                    type:MY_ROOMS_QUEUE_HISTORY_ERROR,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put({
                type: MY_ROOMS_QUEUE_LISTS_ERROR,
                message: err?.message
            })
        }
    })
}


export default function* rootSaga() {
    yield all([
        fork(_getQueueRoomListsFiltered),
        fork(_getMyRoomQueueHistory),
        fork(_getMyRoomQueue),
        fork(_getRoomDetail),
        fork(_roomLists),
        fork(_deleteImageInRoom),
    ])
}
