import { all, takeEvery, put, fork, call } from 'redux-saga/effects';
import {
	AUTH_TOKEN,
	SIGNIN,
	SIGNOUT,
	SIGNUP,
	SIGNIN_WITH_GOOGLE,
	SIGNIN_WITH_FACEBOOK, USER_LOADED, SUCCESS_LOAD_USER, ERROR_LOAD_USER
} from '../constants';
import {
	showAuthMessage,
	authenticated,
	signOutSuccess,
	signUpSuccess,
	signInWithGoogleAuthenticated,
	signInWithFacebookAuthenticated, onNavTypeChange
} from "../actions";

import FirebaseService from 'services/FirebaseService'
import Utils from "../../utils";
import ApiService from "../../services/Api.service";
import {getApiRoute} from "../constants";
import {NAV_TYPE_SIDE, NAV_TYPE_TOP} from "../../constants/ThemeConstant";


export function* LoadUser(){
	yield takeEvery(USER_LOADED, function*({}){
		try{
			let token = Utils.getToken('get')
			const {error,message,data} = yield call(()=> {
				return new Promise((resolve)=> {
					resolve(new ApiService({
						url:getApiRoute('auth/loaduser'),
						config: {
							headers: {
								Authorization: `Bearer ${token}`
							}
						}
					}).get())
				})
			})
			if(error){

				yield all([
					put(onNavTypeChange(NAV_TYPE_TOP)),
					put ({
						type:ERROR_LOAD_USER,
						message: message
					})
				])

				//
			}else{

				if (typeof(data) ==="object" && Object.keys(data).length > 0 && typeof(data?.user?.role) !== "undefined"){
					if(typeof(data?.user?.role) !== "undefined"){
						switch (data?.user?.role) {
							case "admin":
								yield all([
									put({
										type:SUCCESS_LOAD_USER,
										data,
									}),
									put(onNavTypeChange(NAV_TYPE_SIDE))
								])
								break;
							case "villa":
								yield all([
									put({
										type:SUCCESS_LOAD_USER,
										data,
									}),
									put(onNavTypeChange(NAV_TYPE_SIDE))
								])
								break;
							case "customer":
							case "user":
								yield all([
									put({
										type:SUCCESS_LOAD_USER,
										data,
									}),
									put(onNavTypeChange(NAV_TYPE_TOP))
								])
								break;
							default:
								yield all([
									put({
										type:SUCCESS_LOAD_USER,
										data,
									}),
									put(onNavTypeChange(NAV_TYPE_TOP))
								])
								break;
						}
					}
				}else{
					yield all([
						put(onNavTypeChange(NAV_TYPE_TOP)),
						put({
							type:SUCCESS_LOAD_USER,
							data,
						}),
					])
				}
			}


		}catch(err){
			yield all ([
				put(onNavTypeChange(NAV_TYPE_TOP)),
				put ({
					type:ERROR_LOAD_USER,
					message: err.message
				})
			])
		}
	})
}

export function* signInWithFBEmail() {
  yield takeEvery(SIGNIN, function* ({payload}) {
		const {email, password} = payload;
		try {
			const user = yield call(FirebaseService.signInEmailRequest, email, password);
			if (user.message) {
				yield put(showAuthMessage(user.message));
			} else {
				localStorage.setItem(AUTH_TOKEN, user.user.uid);
				yield put(authenticated(user.user.uid));
			}
		} catch (err) {
			yield put(showAuthMessage(err));
		}
	});
}

