import {PUBLIC_GET_TRAVEL_NEARBY, PUBLIC_GET_TRAVEL_SINGLE} from "../../constants";

export const _getTravelNearby = (coordinates = {},params = {})=> ({
    type:PUBLIC_GET_TRAVEL_NEARBY,
    coordinates,params
})

export const _getTravelSingle = (id,params = {},method = 'get')=> ({
    type:PUBLIC_GET_TRAVEL_SINGLE,
    id,params,method
})
