import React from 'react';
import {connect} from 'react-redux';
import Loading from 'components/shared-components/Loading';
import MobileNav from 'components/layout-components/MobileNav'
import PageHeader from 'components/layout-components/PageHeader';
import Footer from 'components/layout-components/Footer';
import {
    Layout,
    Grid,
} from "antd";

import navigationConfig from "configs/NavigationConfig";
import {
    SIDE_NAV_WIDTH,
    SIDE_NAV_COLLAPSED_WIDTH,
    NAV_TYPE_SIDE,
    NAV_TYPE_TOP,
    DIR_RTL,
    DIR_LTR
} from 'constants/ThemeConstant';
import utils from 'utils';
import {useThemeSwitcher} from "react-css-theme-switcher";
import Views from "./Views";
import PublicSideNav from "../../components/layout-components/public/PublicSideNav";
import PublicTopNav from "../../components/layout-components/public/PublicTopNavContent";
import PublicHeaderNav from "../../components/layout-components/public/PublicHeaderNav";
import MetaDefault from "../../components/meta-component/MetaDefault";

const {Content} = Layout;
const {useBreakpoint} = Grid;

export const PublicLayout = (props) => {
    let {loading,navCollapsed, navType, location, direction} = props
    const currentRouteInfo = utils.getRouteInfo(navigationConfig.filter((item)=> item?.isPublic === true), location.pathname)
    const screens = utils.getBreakPoint(useBreakpoint());
    const isMobile = !screens.includes('lg')
    const isNavSide = navType === NAV_TYPE_SIDE
    const isNavTop = navType === NAV_TYPE_TOP
    const getLayoutGutter = () => {
        if (isNavTop || isMobile) {
            return 0
        }
        return navCollapsed ? SIDE_NAV_COLLAPSED_WIDTH : SIDE_NAV_WIDTH
    }

    const {status} = useThemeSwitcher();

    if (status === 'loading') {
        return <Loading cover="page"/>;
    }

    const getLayoutDirectionGutter = () => {
        if (direction === DIR_LTR) {
            return {paddingLeft: getLayoutGutter()}
        }
        if (direction === DIR_RTL) {
            return {paddingRight: getLayoutGutter()}
        }
        return {paddingLeft: getLayoutGutter()}
    }

    MetaDefault({appName:"Skripsi", title:currentRouteInfo?.title ?? "Home"})

    return loading ? <Loading cover={'page'}/>:  (
        <Layout>
            <PublicHeaderNav isMobile={isMobile}/>
            {(isNavTop && !isMobile) ? <PublicTopNav routeInfo={currentRouteInfo}/> : null}
            <Layout className="app-container">
                {(isNavSide && !isMobile) ? <PublicSideNav routeInfo={currentRouteInfo}/> : null}
                <Layout className="app-layout" style={getLayoutDirectionGutter()}>
                        <PageHeader display={currentRouteInfo?.breadcrumb} title={currentRouteInfo?.title}/>
                        <Content>
                            <Views {...props}/>
                        </Content>
                    <Footer/>
                </Layout>
            </Layout>
            {isMobile && <MobileNav/>}
        </Layout>
    )
}

const mapStateToProps = ({theme,auth}) => {
    let { loading, isAuthenticated } = auth
    const {navCollapsed, navType, locale} = theme;
    return {navCollapsed, navType, locale,loading, isAuthenticated}
};

export default connect(mapStateToProps)(React.memo(PublicLayout));
