import React, {lazy, Suspense} from 'react'
import Loading from "../../components/shared-components/Loading";
import {Redirect, Route, Switch} from "react-router-dom";
// import VillaRoute from "../../permissions/VillaRoute";
import PrivateRoute from "../../permissions/PrivateRoute";
import {APP_MY_VILLA_PATH} from "../../configs/AppConfig";


const MyViews = ()=> {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <PrivateRoute path={`${APP_MY_VILLA_PATH}/dashboard`} component={lazy(()=> import("./dashboard/index"))}/>
                <PrivateRoute path={`${APP_MY_VILLA_PATH}/profile`} component={lazy(() => import(`./profile/index`))} />
                <PrivateRoute path={`${APP_MY_VILLA_PATH}/room`} component={lazy(()=> import("./rooms/index"))}/>
                <PrivateRoute path={`${APP_MY_VILLA_PATH}/book`} component={lazy(()=> import("./booking/index"))}/>
                <PrivateRoute path={`${APP_MY_VILLA_PATH}/villa`} component={lazy(()=> import("./villa/villa-views"))}/>
                <Redirect to={`${APP_MY_VILLA_PATH}/dashboard`} from={`${APP_MY_VILLA_PATH}`}/>
            </Switch>
        </Suspense>
    )
}

export default MyViews
