import My from "../mutations/My";
import {
    MY_ERROR_GET_ROOMS,
    MY_GET_ROOMS,
    MY_REMOVE_ROOM_IN_LIST, MY_REMOVE_ROOM_IN_LIST_ERROR,
    MY_REMOVE_ROOM_IN_LIST_SUCCESS,
    MY_SUCCESS_GET_ROOMS
} from "../constants/My";

let { initial,mutate } = My

export default function(state= initial,action){
    let { type } = action
    switch (type){
        case MY_GET_ROOMS:
            return mutate.rooms.list.get(state,action)
        case MY_SUCCESS_GET_ROOMS:
            return mutate.rooms.list.success(state,action)
        case MY_ERROR_GET_ROOMS:
            return mutate.rooms.list.error(state,action)

        case MY_REMOVE_ROOM_IN_LIST:
            return mutate.rooms.list.delete(state,action)
        case MY_REMOVE_ROOM_IN_LIST_SUCCESS:
            return mutate.rooms.list.deleteSuccess(state,action)
        case MY_REMOVE_ROOM_IN_LIST_ERROR:
            return mutate.rooms.list.deleteError(state,action)


        default:
            return state;
    }
}
