const initialState = {
    loading:true,
    message: null,
    error:false,
    type: null,
    data: null,
    selected: [],
    queue: {
        waiting: {
            loading:true,
            message: null,
            error:false,
            type: null,
            params: {},
            pagination:{ },
            data:[]
        },
        all: {
            loading:true,
            message: null,
            error:false,
            type: null,
            params: {},
            pagination:{ },
            data:[]
        },

    },
    collections: {
        search: {
            loading:false,
            message: null,
            error:false,
            type: null,
            params:{
                page:1,
                limit: 10,
            },
            pagination: {
                page:1,
                limit:10,
                max_page:0,
                total_record:0
            },
            data: [],
        },
        lists: {
            loading:true,
            message: null,
            error:false,
            type: null,
            params: {},
            pagination:{ },
            data:[]
        },
    }
}

const Mutate = {
    search: {
        get: function(state,action){
            return {
                ...state,
                collections:{
                    ...state.collections,
                    search: {
                        ...state.collections.search,
                        loading:true,
                        message: null,
                        error:false,
                        type: action?.type ?? null,
                        params: {
                            ...state.collections.search.params,
                            ...action?.query
                        },
                        data: []
                    }
                }
            }
        },
        success: function(state,action){
            return {
                ...state,
                collections:{
                    ...state.collections,
                    search: {
                        ...state.collections.search,
                        loading:false,
                        message: action?.message,
                        error:false,
                        type: action?.type ?? null,
                        params: {
                            ...state.collections.search.params,
                            ...action?.query
                        },
                        pagination: {
                            ...state.collections.search.pagination,
                            ...action?.pagination
                        },
                        data: [
                            ...action?.data
                        ]
                    }
                }
            }
        },
        error:function(state,action){
            return {
                ...state,
                collections: {
                    ...state.collections,
                    search: {
                        ...state.collections.search,
                        loading:false,
                        message: action?.message ?? null,
                        error:true,
                        type: action?.type ?? null,
                        params:{
                            page:1,
                            limit: 10,
                        },
                        pagination: {
                            page:1,
                            limit:10,
                            max_page:0,
                            total_record:0
                        },
                        data: [],
                    }
                }
            }
        }
    },
    single: {
        get: function(state,action){
            return {
                ...state,
                loading:true,
                message: null,
                type: action?.type ?? null,
                error:false,
                data: null
            }
        },
        getSuccess: function(state,action){
            if(typeof(action?.data) !== "undefined"){
                if(typeof(action?.data?.images) !== "undefined" && Array.isArray(action?.data?.images) && action?.data?.images.length > 0){
                    let newImages = []
                    for(let i = 0 ; i < action.data?.images.length;i++){
                        newImages.push({
                            ...action.data.images[i],
                            loading:false
                        })
                    }
                    Reflect.set(action.data,'images',newImages)
                }
            }
            return {
                ...state,
                loading:false,
                message: action?.message ?? null ,
                type: action?.type ?? null,
                error:false,
                data: action?.data ?? null
            }
        },
        getError: function(state,action){
            return {
                ...state,
                loading:false,
                message: action?.message ?? null ,
                type: action?.type ?? null,
                error:true,
                data: action?.data ?? null
            }
        },
        image: {
            delete:function(state,action){
                console.log(action?.imageId,state.data.images)
                if(typeof(action?.imageId) !== "undefined" && typeof(state?.data) !== "undefined" && Object.keys(state?.data).length > 0){
                    if(typeof(state?.data?.images) !== "undefined" && Array.isArray(state?.data?.images) && state?.data?.images.length > 0){
                        console.log('testing')
                        let index = state.data.images.findIndex((item)=> item._id === action?.imageId)
                        console.log({index})
                        if(index >= 0){
                            console.log({loading:state.data.images[index].loading})
                            state.data.images[index].loading = true
                        }
                    }
                }
                console.log(action?.imageId,state.data.images)
                return{
                    ...state,
                    data: {
                        ...state.data,
                        images: state.data?.images
                    }
                }
            },
            deleteError:function(state,action){
                if(typeof(action?.imageId) !== "undefined" && typeof(state?.data) === "object"){
                    if(typeof(state?.data?.images) !== "undefined" && Array.isArray(state?.data?.images) && state?.data?.images.length > 0){
                        let index = state.data.images.findIndex((item)=> item._id === action?.imageId)
                        if(index >= 0){
                            state.data.images[index].loading = false
                        }
                    }
                }
                return{
                    ...state,
                    data: {
                        ...state.data,
                        images: state.data?.images
                    }
                }
            },
            deleteSuccess:function(state,action){
                if(typeof(action?.imageId) !== "undefined" && typeof(state?.data) === "object"){
                    if(typeof(state?.data?.images) !== "undefined" && Array.isArray(state?.data?.images) && state?.data?.images.length > 0){
                        state.data.images = state.data.images.filter((item)=> item._id !== action?.imageId)
                    }
                }
                return{
                    ...state,
                    data: {
                        ...state.data,
                        images: state.data?.images
                    }
                }
            },

        }

    },
    rooms: {
        dynamic: {
            get:function(state,action){
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        lists: {
                            ...state.collections.lists,
                            loading:true,
                            message: null,
                            error:false,
                            type: action?.type ?? null,
                            data: []
                        }
                    }
                }
            },
            getSuccess:function(state,action){
                if(typeof(action?.data) !== "undefined" && Array.isArray(action?.data) && action?.data.length > 0){
                    let newArr = []
                    for(let i =0; i < action.data.length;i++){
                        newArr.push({
                            ...action.data[i],
                            loading:false
                        })
                    }
                    state.collections.lists.data = newArr
                }
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        lists: {
                            ...state.collections.lists,
                            loading:false,
                            message: action?.message ?? null,
                            error:false,
                            type: action?.type ?? null,
                            params: {
                                ...state.collections.lists.params,
                                ...action?.params
                            },
                            pagination: {
                                ...state.collections.lists.pagination,
                                ...action?.pagination
                            },
                            data: [...state.collections.lists.data]
                        }
                    }
                }
            },
            getError:function(state,action){
                return {
                    ...state,
                    collections: {
                        ...state.collections,
                        lists: {
                            ...state.collections.lists,
                            loading:false,
                            message: action?.message ?? null,
                            error:false,
                            type: action?.type ?? null,
                            params: {
                                ...state.collections.lists.params,
                                ...action?.params
                            },
                            pagination: {
                                ...state.collections.lists.pagination,
                                ...action?.pagination
                            },
                            data: [...state.collections.lists.data]
                        }
                    }
                }
            },
        }
    },
    queue: {
        waiting: {
            get:function(state,action){
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        waiting: {
                            ...state.queue.waiting,
                            loading:true,
                            message: null,
                            type: action.type ?? null,
                            error:false,
                            data: []
                        }
                    }
                }
            },
            getSuccess: function(state,action){
                if(typeof(action?.data) !== "undefined" && Array.isArray(action.data) && action.data.length > 0 ){
                    let newArr = []
                    for(let i = 0; i < action.data.length;i++){
                        newArr.push({
                            ...action.data[i],
                            loading:false
                        })
                    }
                    state.queue.waiting.data = newArr
                }
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        waiting: {
                            ...state.queue.waiting,
                            loading: false,
                            message: action?.message ?? null,
                            type: action.type ?? null,
                            error:false,
                            pagination: {
                                ...state.queue.waiting.pagination,
                                ...action?.pagination
                            },
                            params: {
                                ...state.queue.waiting.params,
                                ...action?.params,
                            },
                            data: state.queue.waiting.data,
                        }
                    }
                }
            },
            getError: function(state,action){
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        waiting: {
                            ...state.queue.waiting,
                            loading: false,
                            message: action?.message ?? null,
                            type: action.type ?? null,
                            error:false,
                            data: []
                        }
                    }
                }
            }
        },
        all: {
            get:function(state,action){
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        all: {
                            ...state.queue.all,
                            loading:true,
                            message: null,
                            type: action.type ?? null,
                            error:false,
                            data: []
                        }
                    }
                }
            },
            getSuccess: function(state,action){
                if(typeof(action?.data) !== "undefined" && Array.isArray(action.data) && action.data.length > 0 ){
                    let newArr = []
                    for(let i = 0; i < action.data.length;i++){
                        newArr.push({
                            ...action.data[i],
                            loading:false
                        })
                    }
                    state.queue.all.data = newArr
                }
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        all: {
                            ...state.queue.all,
                            loading: false,
                            message: action?.message ?? null,
                            type: action.type ?? null,
                            error:false,
                            pagination: {
                                ...state.queue.all.pagination,
                                ...action?.pagination
                            },
                            params: {
                                ...state.queue.all.params,
                                ...action?.params,
                            },
                            data: state.queue.all.data,
                        }
                    }
                }
            },
            getError: function(state,action){
                return {
                    ...state,
                    queue: {
                        ...state.queue,
                        all: {
                            ...state.queue.all,
                            loading: false,
                            message: action?.message ?? null,
                            type: action.type ?? null,
                            error:false,
                            data: []
                        }
                    }
                }
            }
        },

    }
}

const Rooms = {
    initialState,
    ...Mutate
}
export default Rooms
