import {
    AUTH_TOKEN,
    AUTHENTICATED,
    SHOW_AUTH_MESSAGE,
    HIDE_AUTH_MESSAGE,
    SIGNOUT_SUCCESS,
    SIGNUP_SUCCESS,
    SHOW_LOADING,
    SIGNIN_WITH_GOOGLE_AUTHENTICATED,
    SIGNIN_WITH_FACEBOOK_AUTHENTICATED, USER_LOADED, SUCCESS_LOAD_USER, ERROR_LOAD_USER
} from '../constants';
import Utils from "../../utils";

const initState = {
    loading: true,
    message: '',
    showMessage: false,
    redirect: '',
    isAuthenticated: !!Utils.getToken('get'),
    token: Utils.getToken('get'),
    data: null
}

const auth = (state = initState, action) => {
    switch (action.type) {
        case USER_LOADED:
            return {
                ...state,
                loading:true,
                isAuthenticated: false,
                token: null,
                data: null
            }
        case SUCCESS_LOAD_USER:
            return {
                ...state,
                loading:false,
                isAuthenticated: true,
                token: Utils.getToken('get'),
                data: action?.data ?? null
            }
        case ERROR_LOAD_USER:
            return {
                ...state,
                loading:false,
                isAuthenticated: false,
                token: null,
                data: null
            }

        case AUTHENTICATED:
            return {
                ...state,
                loading: false,
                redirect: '/',
                token: action.token
            }
        case SHOW_AUTH_MESSAGE:
            return {
                ...state,
                message: action.message,
                showMessage: true,
                loading: false
            }
        case HIDE_AUTH_MESSAGE:
            return {
                ...state,
                message: '',
                showMessage: false,
            }
        case SIGNOUT_SUCCESS: {
            return {
                ...state,
                token: null,
                redirect: '/',
                loading: false
            }
        }
        case SIGNUP_SUCCESS: {
            return {
                ...state,
                loading: false,
                token: action.token
            }
        }
        case SHOW_LOADING: {
            return {
                ...state,
                loading: true
            }
        }
        case SIGNIN_WITH_GOOGLE_AUTHENTICATED: {
            return {
                ...state,
                loading: false,
                token: action.token
            }
        }
        case SIGNIN_WITH_FACEBOOK_AUTHENTICATED: {
            return {
                ...state,
                loading: false,
                token: action.token
            }
        }
        default:
            return state;
    }
}

export default auth
