import {DELETE_IMAGE_AT_ROOMS, GET_QUEUE_ROOMS_LIST_FILTERED, GET_ROOMS_LISTS, GET_ROOMS_SINGLE} from "../../constants";

export const _getQueueRoomsListFiltered = (query = {})=> ({
    type:GET_QUEUE_ROOMS_LIST_FILTERED,
    query
})

export const _getRoomsDetail = (id, params = {},self = '/self')=> ({
    type:GET_ROOMS_SINGLE,
    id,params,self
})

export const _getRoomsLists = (params = {})=> ({
    type:GET_ROOMS_LISTS,
    params
})

export const _deleteImagesRoom= (id,imageId)=> ({
    type:DELETE_IMAGE_AT_ROOMS,
    id,imageId
})