import {villa} from './villa'
import {travel,travelInitialState} from './travel'
import Rooms from './Rooms'
import My from './My'
import Booking from './Booking'
const Mutate = {
    Travel: {
        initialState:travelInitialState,
        ...travel
    },
    Booking,
    My:My,
    Villa: villa,
    Rooms,
}
export default Mutate
