import Utils from "../../utils";

export const initialState = {
    loading:true,
    error:false,
    message: null,
    type: null,
    data: null,
    collections: {
        rooms: {
            add: {
                loading:false,
                message: null,
                error:false,
                fields: {}
            },
            single: {
                loading:true,
                type: null,
                message: null,
                error:false,
                data: null
            },
            list: {
                loading:true,
                message: null,
                type: null,
                error:false,
                pagination: {
                    current_page:1,
                    max_page: 0,
                    total_page:0,
                    total_record:0
                },
                params: {
                    page: 1,
                    limit:10,
                },
                data: []
            }
        }
    }
}


const My = {
    initial:initialState,
    mutate: {
        rooms: {
            list: {
                get: function(state,action){
                    return {
                        ...state,
                        collections: {
                            ...state.collections,
                            rooms: {
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    loading:true,
                                    message: null,
                                    error:false,
                                    type:Utils.ObjResolve(action,"type"),
                                    data: [],
                                }
                            }
                        }
                    }
                },
                success: function(state,action){
                    Utils.MutateDataTemplate({
                        data:action?.data,
                        attributes:{
                            error:false,
                            message: null,
                            loading:false
                        },
                        callback: (data)=> {
                            state.collections.rooms.list.data = data
                        }
                    })
                    return {
                        ...state,
                        collections: {
                            ...state.collections,
                            rooms: {
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    loading:false,
                                    message: Utils.ObjResolve(action,"message"),
                                    error:Utils.ObjResolve(action,"error") ?? false,
                                    type:Utils.ObjResolve(action,"type"),
                                    pagination: {
                                        ...state.collections.rooms.list.pagination,
                                        ...action?.pagination,
                                    },
                                    params: {
                                        ...state.collections.rooms.list.params,
                                        ...action?.params
                                    },
                                    data: [
                                        ...state.collections.rooms.list.data
                                    ],
                                }
                            }
                        }
                    }
                },
                error: function(state,action){
                    return {
                        ...state,
                        collections: {
                            ...state.collections,
                            rooms: {
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    loading:false,
                                    message: Utils.ObjResolve(action,"message"),
                                    error:false,
                                    type:Utils.ObjResolve(action,"type"),
                                    pagination: {
                                        ...initialState.collections.rooms.list.pagination
                                    },
                                    params: {
                                        ...initialState.collections.rooms.list.params,
                                    },
                                    data: [],
                                }
                            }
                        }
                    }
                },
                delete: function(state,action){
                    if(typeof(action?.id) !== "undefined" && action?.id !== "" && action?.id !== null){
                        let index = state.collections.rooms.list.data.findIndex((item)=> item?._id === action?.id)
                        if(index >= 0){
                            state.collections.rooms.list.data[index].loading = true
                        }
                    }
                    return {
                        ...state,
                        collections : {
                            ...state.collections,
                            rooms :{
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    data: state.collections.rooms.list.data
                                }
                            }
                        }
                    }
                },
                deleteSuccess: function(state,action){
                    if(typeof(action?.id) !== "undefined" && action?.id !== "" && action?.id !== null){
                        let index = state.collections.rooms.list.data.findIndex((item)=> item?._id === action?.id)
                        if(index >= 0){
                            state.collections.rooms.list.data[index].loading = false
                        }

                        setTimeout(()=> {
                            state.collections.rooms.list.data.filter((item)=> item?._id !== action?.id)
                        },2000)
                    }

                    return {
                        ...state,
                        collections: {
                            ...state.collections,
                            rooms:{
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    data: state.collections.rooms.list.data
                                }
                            }
                        }
                    }
                },
                deleteError: function(state,action){
                    if(typeof(action?.id) !== "undefined" && action?.id !== "" && action?.id !== null){
                        let index = state.collections.rooms.list.data.findIndex((item)=> item?._id === action?.id)
                        if(index >= 0){
                            state.collections.rooms.list.data[index].loading = false
                        }
                    }

                    return {
                        ...state,
                        collections: {
                            ...state.collections,
                            rooms:{
                                ...state.collections.rooms,
                                list: {
                                    ...state.collections.rooms.list,
                                    data: state.collections.rooms.list.data
                                }
                            }
                        }
                    }
                }
            }
        },
        detail: {
            get:function(state,action){
                return {
                    ...state,
                    detail: {
                        ...state.detail,
                        loading:true,
                        message: null,
                        error:false,
                        type: action?.type ?? null,
                        data: null
                    }
                }
            },
            getSuccess:function(state,action){
                return {
                    ...state,
                    detail: {
                        ...state.detail,
                        loading:false,
                        message: action?.message ?? "Successfully!",
                        error:action?.error ?? false,
                        type: action?.type ?? null,
                        data: action?.data ?? null
                    }
                }
            },
            getError:function(state,action){
                return {
                    ...state,
                    detail: {
                        ...state.detail,
                        loading:false,
                        message: action?.message ?? "Successfully!",
                        error:true,
                        type: action?.type ?? null,
                        data: null
                    }
                }
            }
        }
    }
}




export default My
