import React, {lazy, Suspense} from "react";
import {Switch, Route, Redirect} from "react-router-dom";
import Loading from 'components/shared-components/Loading';
import {APP_PREFIX_PATH} from 'configs/AppConfig'
import AdminRoute from "../../permissions/AdminRoute";

export const AppViews = () => {
    return (
        <Suspense fallback={<Loading cover="content"/>}>
            <Switch>
                <AdminRoute path={`${APP_PREFIX_PATH}/home`} component={lazy(() => import(`./home`))}/>
                <AdminRoute path={`${APP_PREFIX_PATH}/villa`} component={lazy(() => import("./villa/VillaViews"))}/>
                <AdminRoute path={`${APP_PREFIX_PATH}/travel`} component={lazy(() => import("./travel/index"))}/>
                <Redirect from={`${APP_PREFIX_PATH}`} to={`${APP_PREFIX_PATH}/home`}/>
            </Switch>
        </Suspense>
    )
}

export default React.memo(AppViews);
