const initialState = {
    current: {
        lat: null,
        long:null,
    }
}
export default function(state = initialState, action){
    switch (action?.type){
        case "SET_GEO_LOCATION":
            return {
                ...state,
                current: {
                    lat:action?.lat,
                    long:action?.long
                }
            }
        default:
            return state;
    }
}
