import {
    GET_ALL_TAXONOMY, GET_ERROR_ALL_TAXONOMY,
    GET_ERROR_TAXONOMY,
    GET_SUCCESS_ALL_TAXONOMY,
    GET_SUCCESS_TAXONOMY,
    GET_TAXONOMY
} from "../constants";

const initialState = {
    loading:true,
    message: null,
    type: null,
    error:false,
    data:{},
    collections: {
        lists: {
            loading:true,
            message: null,
            type: null,
            error:false,
            pagination:{},
            params: {},
            data: []
        }
    }
}

export default function (state = initialState, action){
    let { type } = action
    switch (type){
        case GET_TAXONOMY:
            return {
                ...state,
                loading:true,
                message: null,
                type,
                error:false,
                data: {}
            }
        case GET_SUCCESS_TAXONOMY:
            return {
                ...state,
                loading:false,
                message: action?.message ?? null,
                error:false,
                type,
                data: {
                    ...state.data,
                    ...action?.data,
                },
            }
        case GET_ERROR_TAXONOMY:
            return {
                ...state,
                loading:false,
                message: action?.message ??null,
                error:true,
                type,
                data: {}
            }

        case GET_ALL_TAXONOMY:
            return {
                ...state,
                collections: {
                    ...state.collections,
                    lists: {
                        ...state.collections.lists,
                        loading:true,
                        message: null,
                        error:false,
                        type,
                        data:[]
                    }
                }
            }
        case GET_SUCCESS_ALL_TAXONOMY:
            return {
                ...state,
                collections: {
                    ...state.collections,
                    lists: {
                        ...state.collections.lists,
                        loading:false,
                        message: action?.message ?? null,
                        error:false,
                        type,
                        pagination: {
                            ...state.collections.lists.pagination,
                            ...action?.pagination
                        },
                        params: {
                            ...state.collections.lists.params,
                            ...action?.params,
                        },
                        data: [
                            ...action?.data
                        ]
                    }
                }
            }
        case GET_ERROR_ALL_TAXONOMY:
            return {
                ...state,
                collections: {
                    ...state.collections,
                    lists: {
                        ...state.collections.lists,
                        loading:false,
                        message: action?.message ?? null,
                        type,
                        error:true,
                        data: []
                    }
                }
            }
        default:
            return state
    }
}
