import {Locations} from "../mutations/Locations";
import {
    ERROR_GET_DISTRICTS_LISTS_OF_KARAWANG,
    GET_DISTRICTS_LISTS_OF_KARAWANG,
    SUCCESS_GET_DISTRICTS_LISTS_OF_KARAWANG,
    ERROR_GET_DISTRICTS,
    ERROR_GET_PROVINCES, ERROR_GET_REGENCIES, ERROR_GET_VILAGES, ERROR_LOAD_MORE_GET_PROVINCES, GET_DISTRICTS,
    GET_PROVINCES, GET_REGENCIES, GET_VILAGES,
    LOAD_MORE_GET_PROVINCES, SUCCESS_GET_DISTRICTS,
    SUCCESS_GET_PROVINCES, SUCCESS_GET_REGENCIES, SUCCESS_GET_VILAGES,
    SUCCESS_LOAD_MORE_GET_PROVINCES
} from "../constants";

export default function(state = Locations.initialState,action){
    let { type } = action

    switch (type) {
        case GET_DISTRICTS_LISTS_OF_KARAWANG:
            return Locations.districts.karawang.get(state,action)
        case SUCCESS_GET_DISTRICTS_LISTS_OF_KARAWANG:
            return Locations.districts.karawang.success(state,action)
        case ERROR_GET_DISTRICTS_LISTS_OF_KARAWANG:
            return Locations.districts.karawang.error(state,action)




        case GET_PROVINCES:
            return {
                ...state,
                province:{
                    ...state.province,
                    loading:true,
                    type: action?.type,
                    message: null,
                    error:false,
                    data: []
                }
            }
        case SUCCESS_GET_PROVINCES:
            return {
                ...state,
                province: {
                    ...state.province,
                    loading:false,
                    message: action?.message,
                    type: action?.type,
                    error:false,
                    pagination:{
                        ...state.province.pagination,
                        ...action?.pagination,
                    },
                    params: {
                        ...state.province.params,
                        ...action.params,
                    },
                    data: [...action?.data]
                }
            }
        case ERROR_GET_PROVINCES:
            return {
                ...state,
                province:{
                    ...state.province,
                    loading:false,
                    message: action?.message,
                    pagination:{
                        ...state.province.pagination,
                        ...Locations.initialState.province.pagination
                    },
                    params:{
                        ...state.province.params,
                        ...Locations.initialState.province.params
                    },
                    error:true,
                    type: action?.type,
                    data: []
                }
            }

        case LOAD_MORE_GET_PROVINCES:
            return {
                ...state,
                province: {
                    ...state,
                    loading_more: true,
                    message: action?.message,
                    type: action?.type,
                    error:false,
                    pagination:{
                        ...state.province.pagination,
                        ...action?.pagination,
                    },
                    params: {
                        ...state.province.params,
                        ...action.params,
                    }
                }
            }
        case SUCCESS_LOAD_MORE_GET_PROVINCES:
            return {
                ...state,
                province: {
                    ...state,
                    loading_more:false,
                    message: action?.message,
                    type: action?.type,
                    error:false,
                    pagination:{
                        ...state.province.pagination,
                        ...action?.pagination,
                    },
                    params: {
                        ...state.province.params,
                        ...action.params,
                    },
                    data: [
                        ...state.province.data,
                        ...action?.data
                    ]
                }
            }
        case ERROR_LOAD_MORE_GET_PROVINCES:
            return {
                ...state,
                province:{
                    ...state.province,
                    loading_more:false,
                    message:action?.message ?? null,
                    type: action?.type ?? null,
                    error:true,
                    data:[...state.province.data]
                }
            }

        case GET_REGENCIES:
            return {
                ...state,
                regencies: {
                    ...state,
                    loading:true,
                    message: null,
                    error:false,
                    type:action?.type,
                    data: []
                }
            }
        case SUCCESS_GET_REGENCIES:
            return {
                ...state,
                regencies: {
                    ...state.regencies,
                    loading:false,
                    message: action?.message ?? null,
                    error:false,
                    type: action?.type,
                    pagination: {
                        ...state.regencies.pagination,
                        ...action?.pagination,
                    },
                    params: {
                        ...state.regencies.params,
                        ...action?.params
                    },
                    data: [...action?.data]
                }
            }
        case ERROR_GET_REGENCIES:
            return {
                ...state,
                regencies: {
                    ...state.regencies,
                    loading:false,
                    error:true,
                    message: action?.message,
                    data: []
                }
            }

        case GET_DISTRICTS:
            return {
                ...state,
                districts: {
                    ...state.districts,
                    loading:false,
                    message: null,
                    error:false,
                    type: action?.type,
                    data: [],
                }
            }
        case SUCCESS_GET_DISTRICTS:
            return {
                ...state,
                districts: {
                    ...state.districts,
                    loading:false,
                    message: action?.message ?? null,
                    type: action?.type ?? null,
                    error:false,
                    pagination: {
                        ...state.districts.pagination,
                        ...action?.pagination,
                    },
                    params: {
                        ...state.districts.params,
                        ...action?.params
                    },
                    data: [...action?.data]
                }
            }
        case ERROR_GET_DISTRICTS:
            return {
                ...state,
                districts: {
                    ...state.districts,
                    loading:false,
                    message: action?.message ?? null,
                    type: action?.type,
                    error:true,
                    data: []
                }
            }

        case GET_VILAGES:
            return {
                ...state,
                vilages: {
                    ...state.vilages,
                    loading:true,
                    message: null,
                    error:false,
                    data: []
                }
            }
        case SUCCESS_GET_VILAGES:
            return {
                ...state,
                vilages: {
                    ...state.vilages,
                    loading:false,
                    type: action?.type ?? null,
                    message: action?.message ?? null,
                    error:false,
                    pagination:{
                        ...state.vilages.pagination,
                        ...action?.pagination
                    },
                    params: {
                        ...state.vilages.params,
                        ...action?.params,
                    },
                    data: [...action?.data]
                }
            }
        case ERROR_GET_VILAGES:
            return {
                ...state,
                vilages: {
                    ...state.vilages,
                    loading: false,
                    message:action?.message ?? null,
                    error:false,
                    type: action?.type ?? null,
                    data: []
                }
            }
        default:
            return state;
    }
}
