const Booking = {
    mutate: {
        public: {
            create: function(state,action){},
            createSuccess: function(state,action){},
            createError: function(state,action){},

            lists: {
                get: function (state,action){
                    return {
                        ...state,
                        collections :{
                            ...state.collections,
                            lists: {
                                ...state.collections.lists,
                                loading:true,
                                data: []
                            }
                        }
                    }
                },
                getSuccess: function (state,action){
                    if(typeof(action?.data) !== "undefined" && Array.isArray(action?.data)  && action.data.length > 0){
                        let newArr = []

                        for(let i = 0 ; i < action.data.length ; i++){
                            newArr.push({
                                ...action.data[i],
                                loading:false
                            })
                        }

                        state.collections.lists.data = newArr
                    }
                    return {
                        ...state,
                        collections :{
                            ...state.collections,
                            lists: {
                                ...state.collections.lists,
                                loading:false,
                                error:false,
                                message: action?.message ?? null,
                                type: action.type ?? null,
                                params: {
                                    ...state.collections.lists.params,
                                    ...action?.params,
                                },
                                pagination: {
                                    ...state.collections.lists.pagination,
                                    ...action?.pagination,
                                },
                                data: state.collections.lists.data,
                            }
                        }
                    }
                },
                getError: function (state,action){
                    return {
                        ...state,
                        collections :{
                            ...state.collections,
                            lists: {
                                ...state.collections.lists,
                                loading:false,
                                error:true,
                                message: action?.message ?? null,
                                type: action.type ?? null,
                                data: []
                            }
                        }
                    }
                },
            },
            detail: {
                get: function (state,action){},
                getSuccess: function (state,action){},
                getError: function (state,action){},
            }

        }
    }
}

export default Booking