import {all, takeEvery, put, fork, call} from 'redux-saga/effects';
import {MY_ERROR_GET_ROOMS, MY_GET_ROOMS, MY_REMOVE_ROOM_IN_LIST, MY_SUCCESS_GET_ROOMS} from "../constants/My";
import ApiService from "../../services/Api.service";
import {GET_MY_VILLA_PROFILE, GET_MY_VILLA_PROFILE_ERROR, GET_MY_VILLA_PROFILE_SUCCESS} from "../constants";

//
export function* _getMyRooms(){
    yield takeEvery(MY_GET_ROOMS, function*({query}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(new ApiService({
                        url:"/api/v1/villa/self/rooms/lists",
                        config: {
                            params: {...query},
                        }
                    }).get())
                })
            })
            if(!response.error){
                yield put ({
                    type: MY_SUCCESS_GET_ROOMS,
                    ...response,
                })
            }else{
                yield put({
                    type:MY_ERROR_GET_ROOMS,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put({
                type:MY_ERROR_GET_ROOMS,
                message: err.message
            })
        }
    })

}

export function* _removeRoomInList(){
    yield takeEvery(MY_REMOVE_ROOM_IN_LIST, function*({id}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:"/api/v1/room/"
                        }).delete()
                    )
                })
            })
        }catch(err){

        }
    })
}

/**
 * @access - [ villa ]
 * @returns {Generator<*, void, *>}
 */
export function* _getMyVillaProfile(){
    yield takeEvery(GET_MY_VILLA_PROFILE, function*({params}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:`/api/v1/villa/self/profile`,
                            config: {
                                params: {
                                    ...params
                                }
                            }
                        })
                            .get()
                    )
                })
            })

            if(!response?.error){
                yield put({
                    type:GET_MY_VILLA_PROFILE_SUCCESS,
                    ...response
                })
            }else{
                yield put ({
                    type:GET_MY_VILLA_PROFILE_ERROR,
                    message: response?.message ?? "Some error"
                })
            }
        }catch(err){
            yield put ({
                type:GET_MY_VILLA_PROFILE_ERROR,
                message: err.message ?? "Some error"
            })
        }
    })
}

export default function* rootSaga() {
    yield all([
        fork(_getMyRooms),
        fork(_getMyVillaProfile),
    ])
}
