import { all, takeEvery, put, fork, call } from 'redux-saga/effects';
import {
    GET_TAXONOMY,
    GET_SUCCESS_TAXONOMY,
    GET_ERROR_TAXONOMY, GET_ALL_TAXONOMY, GET_SUCCESS_ALL_TAXONOMY, GET_ERROR_ALL_TAXONOMY
} from "../constants";
import ApiService from "../../services/Api.service";

export function* _singleTaxonomy(){
    yield takeEvery(GET_TAXONOMY, function*({value,orderBy}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:`/api/v1/category/${value}`,
                            config:{
                                params: {
                                    orderBy,
                                }
                            }
                        }).get()
                    )
                })
            })

            console.log({response})

            if(!response?.error){
                yield put({
                    type:GET_SUCCESS_TAXONOMY,
                    ...response
                })
            }else{
                yield put({
                    type: GET_ERROR_TAXONOMY,
                    message: response?.message ?? "Some Error"
                })
            }

        }catch(err){
            yield put({
                type: GET_ERROR_TAXONOMY,
                message: err?.message ?? "Some Error"
            })
        }
    })
}

export function* _getAllTaxonomy(){
    yield takeEvery(GET_ALL_TAXONOMY, function*({params}){
        try{
            const response = yield call(()=> {
                return new Promise((resolve)=> {
                    resolve(
                        new ApiService({
                            url:'/api/v1/category',
                            config: {
                                params,
                            }
                        }).get()
                    )
                })
            })
            if(!response?.error){
                yield put ({
                    type: GET_SUCCESS_ALL_TAXONOMY,
                    ...response
                })
            }else{
                yield put ({
                    type:GET_ERROR_ALL_TAXONOMY,
                    message: response?.message ?? "Some Error"
                })
            }
        }catch(err){
            yield put ({
                type:GET_ERROR_ALL_TAXONOMY,
                message: err?.message ?? "Some Error"
            })
        }
    })
}

export default function* rootSaga() {
    yield all([
        fork(_singleTaxonomy),
        fork(_getAllTaxonomy)

    ])
}
